n.CodeMirror pre {\n  padding: 0 4px; /* Horizontal padding of content */\n}\n\n.CodeMirror-scrollbar-filler, .CodeMirror-gutter-filler {\n  background-color: white; /* The little square between H and V scrollbars */\n}\n\n/* GUTTER */\n\n.CodeMirror-gutters {\n  border-right: 1px solid #ddd;\n  background-color: #f7f7f7;\n  white-space: nowrap;\n}\n.CodeMirror-linenumbers {}\n.CodeMirror-linenumber {\n  padding: 0 3px 0 5px;\n  min-width: 20px;\n  text-align: right;\n  color: #999;\n  white-space: nowrap;\n}\n\n.CodeMirror-guttermarker { color: black; }\n.CodeMirror-guttermarker-subtle { color: #999; }\n\n/* CURSOR */\n\n.CodeMirror-cursor {\n  border-left: 1px solid black;\n  border-right: none;\n  width: 0;\n}\n/* Shown when moving in bi-directional text */\n.CodeMirror div.CodeMirror-secondarycursor {\n  border-left: 1px solid silver;\n}\n.cm-fat-cursor .CodeMirror-cursor {\n  width: auto;\n  border: 0 !important;\n  background: #7e7;\n}\n.cm-fat-cursor div.CodeMirror-cursors {\n  z-index: 1;\n}\n\n.cm-animate-fat-cursor {\n  width: auto;\n  border: 0;\n  -webkit-animation: blink 1.06s steps(1) infinite;\n  -moz-animation: blink 1.06s steps(1) infinite;\n  animation: blink 1.06s steps(1) infinite;\n  background-color: #7e7;\n}\n@-moz-keyframes blink {\n  0% {}\n  50% { background-color: transparent; }\n  100% {}\n}\n@-webkit-keyframes blink {\n  0% {}\n  50% { background-color: transparent; }\n  100% {}\n}\n@keyframes blink {\n  0% {}\n  50% { background-color: transparent; }\n  100% {}\n}\n\n/* Can style cursor different in overwrite (non-insert) mode */\n.CodeMirror-overwrite .CodeMirror-cursor {}\n\n.cm-tab { display: inline-block; text-decoration: inherit; }\n\n.CodeMirror-rulers {\n  position: absolute;\n  left: 0; right: 0; top: -50px; bottom: -20px;\n  overflow: hidden;\n}\n.CodeMirror-ruler {\n  border-left: 1px solid #ccc;\n  top: 0; bottom: 0;\n  position: absolute;\n}\n\n/* DEFAULT THEME */\n\n.cm-s-default .cm-header {color: blue;}\n.cm-s-default .cm-quote {color: #090;}\n.cm-negative {color: #d44;}\n.cm-positive {color: #292;}\n.cm-header, .cm-strong {font-weight: bold;}\n.cm-em {font-style: italic;}\n.cm-link {text-decoration: underline;}\n.cm-strikethrough {text-decoration: line-through;}\n\n.cm-s-default .cm-keyword {color: #708;}\n.cm-s-default .cm-atom {color: #219;}\n.cm-s-default .cm-number {color: #164;}\n.cm-s-default .cm-def {color: #00f;}\n.cm-s-default .cm-variable,\n.cm-s-default .cm-punctuation,\n.cm-s-default .cm-property,\n.cm-s-default .cm-operator {}\n.cm-s-default .cm-variable-2 {color: #05a;}\n.cm-s-default .cm-variable-3 {color: #085;}\n.cm-s-default .cm-comment {color: #a50;}\n.cm-s-default .cm-string {color: #a11;}\n.cm-s-default .cm-string-2 {color: #f50;}\n.cm-s-default .cm-meta {color: #555;}\n.cm-s-default .cm-qualifier {color: #555;}\n.cm-s-default .cm-builtin {color: #30a;}\n.cm-s-default .cm-bracket {color: #997;}\n.cm-s-default .cm-tag {color: #170;}\n.cm-s-default .cm-attribute {color: #00c;}\n.cm-s-default .cm-hr {color: #999;}\n.cm-s-default .cm-link {color: #00c;}\n\n.cm-s-default .cm-error {color: #f00;}\n.cm-invalidchar {color: #f00;}\n\n.CodeMirror-composing { border-bottom: 2px solid; }\n\n/* Default styles for common addons */\n\ndiv.CodeMirror span.CodeMirror-matchingbracket {color: #0f0;}\ndiv.CodeMirror span.CodeMirror-nonmatchingbracket {color: #f22;}\n.CodeMirror-matchingtag { background: rgba(255, 150, 0, .3); }\n.CodeMirror-activeline-background {background: #e8f2ff;}\n\n/* STOP */\n\n/* The rest of this file contains styles related to the mechanics of\n   the editor. You probably shouldn't touch them. */\n\n.CodeMirror {\n  position: relative;\n  overflow: hidden;\n  background: white;\n}\n\n.CodeMirror-scroll {\n  overflow: scroll !important; /* Things will break if this is overridden */\n  /* 30px is the magic margin used to hide the element's real scrollbars */\n  /* See overflow: hidden in .CodeMirror */\n  margin-bottom: -30px; margin-right: -30px;\n  padding-bottom: 30px;\n  height: 100%;\n  outline: none; /* Prevent dragging from highlighting the element */\n  position: relative;\n}\n.CodeMirror-sizer {\n  position: relative;\n  border-right: 30px solid transparent;\n}\n\n/* The fake, visible scrollbars. Used to force redraw during scrolling\n   before actual scrolling happens, thus preventing shaking and\n   flickering artifacts. */\n.CodeMirror-vscrollbar, .CodeMirror-hscrollbar, .CodeMirror-scrollbar-filler, .CodeMirror-gutter-filler {\n  position: absolute;\n  z-index: 6;\n  display: none;\n}\n.CodeMirror-vscrollbar {\n  right: 0; top: 0;\n  overflow-x: hidden;\n  overflow-y: scroll;\n}\n.CodeMirror-hscrollbar {\n  bottom: 0; left: 0;\n  overflow-y: hidden;\n  overflow-x: scroll;\n}\n.CodeMirror-scrollbar-filler {\n  right: 0; bottom: 0;\n}\n.CodeMirror-gutter-filler {\n  left: 0; bottom: 0;\n}\n\n.CodeMirror-gutters {\n  position: absolute; left: 0; top: 0;\n  min-height: 100%;\n  z-index: 3;\n}\n.CodeMirror-gutter {\n  white-space: normal;\n  height: 100%;\n  display: inline-block;\n  vertical-align: top;\n  margin-bottom: -30px;\n}\n.CodeMirror-gutter-wrapper {\n  position: absolute;\n  z-index: 4;\n  background: none !important;\n  border: none !important;\n}\n.CodeMirror-gutter-background {\n  position: absolute;\n  top: 0; bottom: 0;\n  z-index: 4;\n}\n.CodeMirror-gutter-elt {\n  position: absolute;\n  cursor: default;\n  z-index: 4;\n}\n.CodeMirror-gutter-wrapper {\n  -webkit-user-select: none;\n  -moz-user-select: none;\n  user-select: none;\n}\n\n.CodeMirror-lines {\n  cursor: text;\n  min-height: 1px; /* prevents collapsing before first draw */\n}\n.CodeMirror pre {\n  /* Reset some styles that the rest of the page might have set */\n  -moz-border-radius: 0; -webkit-border-radius: 0; border-radius: 0;\n  border-width: 0;\n  background: transparent;\n  font-family: inherit;\n  font-size: inherit;\n  margin: 0;\n  white-space: pre;\n  word-wrap: normal;\n  line-height: inherit;\n  color: inherit;\n  z-index: 2;\n  position: relative;\n  overflow: visible;\n  -webkit-tap-highlight-color: transparent;\n  -webkit-font-variant-ligatures: contextual;\n  font-variant-ligatures: contextual;\n}\n.CodeMirror-wrap pre {\n  word-wrap: break-word;\n  white-space: pre-wrap;\n  word-break: normal;\n}\n\n.CodeMirror-linebackground {\n  position: absolute;\n  left: 0; right: 0; top: 0; bottom: 0;\n  z-index: 0;\n}\n\n.CodeMirror-linewidget {\n  position: relative;\n  z-index: 2;\n  overflow: auto;\n}\n\n.CodeMirror-widget {}\n\n.CodeMirror-code {\n  outline: none;\n}\n\n/* Force content-box sizing for the elements where we expect it */\n.CodeMirror-scroll,\n.CodeMirror-sizer,\n.CodeMirror-gutter,\n.CodeMirror-gutters,\n.CodeMirror-linenumber {\n  -moz-box-sizing: content-box;\n  box-sizing: content-box;\n}\n\n.CodeMirror-measure {\n  position: absolute;\n  width: 100%;\n  height: 0;\n  overflow: hidden;\n  visibility: hidden;\n}\n\n.CodeMirror-cursor {\n  position: absolute;\n  pointer-events: none;\n}\n.CodeMirror-measure pre { position: static; }\n\ndiv.CodeMirror-cursors {\n  visibility: hidden;\n  position: relative;\n  z-index: 3;\n}\ndiv.CodeMirror-dragcursors {\n  visibility: visible;\n}\n\n.CodeMirror-focused div.CodeMirror-cursors {\n  visibility: visible;\n}\n\n.CodeMirror-selected { background: #d9d9d9; }\n.CodeMirror-focused .CodeMirror-selected { background: #d7d4f0; }\n.CodeMirror-crosshair { cursor: crosshair; }\n.CodeMirror-line::selection, .CodeMirror-line > span::selection, .CodeMirror-line > span > span::selection { background: #d7d4f0; }\n.CodeMirror-line::-moz-selection, .CodeMirror-line > span::-moz-selection, .CodeMirror-line > span > span::-moz-selection { background: #d7d4f0; }\n\n.cm-searching {\n  background: #ffa;\n  background: rgba(255, 255, 0, .4);\n}\n\n/* Used to force a border model for a node */\n.cm-force-border { padding-right: .1px; }\n\n@media print {\n  /* Hide the cursor when printing */\n  .CodeMirror div.CodeMirror-cursors {\n    visibility: hidden;\n  }\n}\n\n/* See issue #2901 */\n.cm-tab-wrap-hack:after { content: ''; }\n\n/* Help users use markselection to safely style text background */\nspan.CodeMirror-selectedtext { background: none; }\n",":root {\n  font-size: $base-size + px;\n  text-rendering: optimizeLegibility;\n}","body {\n  font-family: $bodyFont;\n  font-size: $base-size + px;\n}\n\nh1, h2, h3, h4, h5, h6,\n.mega, .giga, .kilo {\n  font-family: $headerFont;\n}","/// @prop {number} $bpXS [30em] - equals viewport min-width 480px (smartphone)\n/// @group responsive\n$bpXs: 30em !default;\n\n/// @prop {number} $bpSm [48em] - equals viewport min-width 768px (tablet)\n/// @group responsive\n$bpSm: 48em !default;\n\n/// @prop {number} $bpMd [60em] - equals viewport min-width 960px (medium devices)\n/// @group responsive\n$bpMd: 60em !default;\n\n/// @prop {number} $bpLg [70em] - equals viewport min-width 1120px (large devices)\n/// @group responsive\n$bpLg: 70em !default;\n\n/// @prop {number} $bpXl [80em] - equals viewport min-width 1280px (very large devices)\n/// @group responsive\n$bpXl: 80em !default;\n\n/// @prop {number} $bpXsMax [$bpSm - 1] - equals viewport max-width 767px (smartphone)\n/// @group responsive\n$bpXsMax: ($bpSm - 1) !default;\n\n/// @prop {number} $bpSmMax [$bpMd - 1] - equals viewport max-width 959px (tablet)\n/// @group responsive\n$bpSmMax: ($bpMd - 1) !default;\n\n/// @prop {number} $bpMdMax [$bpLg - 1] - equals viewport max-width 1119px (large devices)\n/// @group responsive\n$bpMdMax: ($bpLg - 1) !default;\n\n/// @prop {number} $bpLgMax [$bpXl - 1] - equals viewport max-width 1279px (very large devices)\n/// @group responsive\n$bpLgMax: ($bpXl - 1) !default;\n\n\n/// @prop {string} $bodyFont [sans-serif] - font-family used in body\n/// @group typo\n$bodyFont: sans-serif !default;\n\n/// @prop {string} $headerFont [sans-serif] - font-family used for h1, h2 ,h3 ,h4, h5, h6\n/// @group typo\n$headerFont: sans-serif !default;\n\n/// @prop {string} $monoFont [monospace] - font-family used for <code>\n/// @group typo\n$monoFont: monospace !default;\n\n/// @prop {number} $base-size [16] - default size used for global size calculations\n$base-size: 16;\n\n/// @prop {string} $headings-weight [bold] - font-weight of h1, h2 ,h3 ,h4, h5, h6\n/// @group typo\n$headings-weight: bold !default;\n\n/// @prop {number} $h1-size [48] - font-size of h1\n/// @group typo\n$h1-size: 48;\n\n/// @prop {number} $h1-height [56] - line-height of h1\n/// @group typo\n$h1-height: 56;\n\n/// @prop {number} $h2-size [40] - font-size of h2\n/// @group typo\n$h2-size: 40;\n\n/// @prop {number} $h2-height [48] - line-height of h2\n/// @group typo\n$h2-height: 48;\n\n/// @prop {number} $h3-size [32] - font-size of h3\n/// @group typo\n$h3-size: 32;\n\n/// @prop {number} $h3-height [40] - line-height of h3\n/// @group typo\n$h3-height: 40;\n\n/// @prop {number} $h4-size [24] - font-size of h4\n/// @group typo\n$h4-size: 24;\n\n/// @prop {number} $h4-height [32] - line-height of h4\n/// @group typo\n$h4-height: 32;\n\n/// @prop {number} $h5-size [20] - font-size of h5\n/// @group typo\n$h5-size: 20;\n\n/// @prop {number} $h5-height [28] - line-height of h5\n/// @group typo\n$h5-height: 28;\n\n/// @prop {number} $h6-size [16] - font-size of h6\n/// @group typo\n$h6-size: 16;\n\n/// @prop {number} $h6-height [24] - line-height of h6\n/// @group typo\n$h6-height: 24;\n","//\n// atGrid.css - CSS Grid System with HTML Attributes\n// =============================================================================\n// https://github.com/jameskolce/atGrid\n//\n// Copyright 2014 James Kolce <contact@jameskolce.com>\n// Licensed under MIT License\n// (https://github.com/jameskolce/atGrid/blob/master/LICENSE)\n//\n\n\n//\n// Variables\n// ---------\n\n// Attribute prefix\n$prefix: \"data-\" !default;\n\n// Max width for container\n$containerWidth: 1200px !default;\n\n// Gutter size in pixels (without the unit we can do math easily)\n$gutter: 32 !default;\n\n// Number of columns in a row\n$numColumns: 12 !default;\n\n// If you only want to use the mixins for \"semantic grids\" set this to true\n$onlySemantic: false !default;\n\n$responsive: true;\n\n//\n// Mixins\n// =============================================================================\n// You can use this to create \"semantic grids\"\n//\n\n//\n// Container\n// ---------\n// Use this mixin to create a row. Rows should be contained in a\n// \"container\" element\n//\n\n@mixin container() {\n  box-sizing: border-box;\n  margin: 0 auto;\n  max-width: $containerWidth;\n  padding-left: ($gutter / 2) + px;\n  padding-right: ($gutter / 2) + px;\n  width: 100%;\n}\n\n//\n// Rows\n// ----\n// Use this mixin to create a row. Columns should be contained in rows\n//\n// @clear-gutter: Set true to contain columns with gutter\n//\n\n@mixin row($clearGutter: true) {\n  @if $clearGutter {\n    margin-left: ($gutter / -2) + px;\n    margin-right: ($gutter / -2) + px;\n  }\n\n  margin-bottom: 0;\n\n  &:before,\n  &:after {\n    content: \"\";\n    display: table;\n  }\n\n  &:after {\n    clear: both;\n  }\n}\n\n//\n// Columns\n// -------\n// Use this mixin to create a column\n//\n// @size: The size of the column, should be a number between 0-1. Example: 1/2\n// @add-gutter: Set this to true if you want to create padding for the column\n//\n\n@mixin column($size, $addGutter: true) {\n  box-sizing: border-box;\n  position: relative;\n  min-height: 1px;\n  float: left;\n  margin-bottom: 0;\n  width: 100%;\n\n  @if $size < 1 {\n    @media (min-width: $bpSmall) {\n      width: ($size * 100%);\n    }\n  }\n\n  @if $addGutter {\n    padding-left: ($gutter / 2) + px;\n    padding-right: ($gutter / 2) + px;\n  }\n}\n\n//\n// Column offset\n// -------\n// Use this mixin to move a column\n//\n// @offset: The size of the offset, should be a number between 0-1. Example: 1/2\n//\n\n@mixin columnOffset($offset) {\n  margin-left: $offset * 100%;\n}\n\n//\n// Semantic Styles\n// =============================================================================\n\n@if not $onlySemantic {\n  //\n  // General Styles\n  // --------------\n  // Basic styles for the container, rows and columns\n  //\n  // Put the columns inside the rows, and the rows inside a container.\n  //\n\n  [#{$prefix}container] {\n    @include container();\n  }\n\n  [#{$prefix}container=\"full\"] {\n    @include container();\n    max-width: 100% !important;\n\n    > [#{$prefix}row] {\n      > [#{$prefix}col~=\"12\"] {\n        padding-right: 0;\n        padding-left: 0;\n      }\n    }\n  }\n\n  [#{$prefix}row] {\n    @include row();\n  }\n\n  //\n  // Column Styles\n  // -------------\n  // Creates the width of the columns and their offset\n  //\n  // To use this grid system you need to use the following syntax in your\n  // HTML files (This example uses default configuration):\n  //\n  // <main container>\n  //   <div row>\n  //     <section column=\"6\">\n  //       <!-- Column with a width of 6 columns -->\n  //     </section>\n  //\n  //     <section column=\"4 +2\">\n  //       <!-- Column with a width of 4 columns and an offset of 2 columns -->\n  //     </section>\n  //   </div>\n  // </main>\n  //\n\n  [#{$prefix}col] {\n    @include column(1);\n  }\n\n  // Create columns\n  @for $i from 1 through $numColumns {\n    [#{$prefix}col~=\"#{$i}\"] {\n      width: floor(($i / $numColumns) * 100%);\n    }\n  }\n\n  @for $i from 1 through $numColumns {\n    [#{$prefix}col~=\"#{$i}xs\"] {\n      @media (min-width: $bpXs) {\n        width: floor(($i / $numColumns) * 100%);\n      }\n    }\n  }\n\n  @for $i from 1 through $numColumns {\n    [#{$prefix}col~=\"#{$i}sm\"] {\n      @media (min-width: $bpSm) {\n        width: floor(($i / $numColumns) * 100%);\n      }\n    }\n  }\n\n  @for $i from 1 through $numColumns {\n    [#{$prefix}col~=\"#{$i}md\"] {\n      @media (min-width: $bpMd) {\n        width: floor(($i / $numColumns) * 100%);\n      }\n    }\n  }\n\n  @for $i from 1 through $numColumns {\n    [#{$prefix}col~=\"#{$i}lg\"] {\n      @media (min-width: $bpLg) {\n        width: floor(($i / $numColumns) * 100%);\n      }\n    }\n  }\n\n  @for $i from 1 through $numColumns {\n    [#{$prefix}col~=\"#{$i}xl\"] {\n      @media (min-width: $bpXl) {\n        width: floor(($i / $numColumns) * 100%);\n      }\n    }\n  }\n\n  // Offset for columns\n  @for $i from 1 through ($numColumns - 1) {\n    [#{$prefix}col~=\"+#{$i}\"] {\n      margin-left: (($i / $numColumns) * 100%);\n    }\n  }\n\n  @for $i from 1 through ($numColumns - 1) {\n    [#{$prefix}col~=\"+#{$i}xs\"] {\n      @media (min-width: $bpXs) {\n        margin-left: (($i / $numColumns) * 100%);\n      }\n    }\n  }\n\n  @for $i from 1 through ($numColumns - 1) {\n    [#{$prefix}col~=\"+#{$i}sm\"] {\n      @media (min-width: $bpSm) {\n        margin-left: (($i / $numColumns) * 100%);\n      }\n    }\n  }\n\n  @for $i from 1 through ($numColumns - 1) {\n    [#{$prefix}col~=\"+#{$i}md\"] {\n      @media (min-width: $bpMd) {\n        margin-left: (($i / $numColumns) * 100%);\n      }\n    }\n  }\n\n  @for $i from 1 through ($numColumns - 1) {\n    [#{$prefix}col~=\"+#{$i}lg\"] {\n      @media (min-width: $bpLg) {\n        margin-left: (($i / $numColumns) * 100%);\n      }\n    }\n  }\n\n  @for $i from 1 through ($numColumns - 1) {\n    [#{$prefix}col~=\"+#{$i}xl\"] {\n      @media (min-width: $bpXl) {\n        margin-left: (($i / $numColumns) * 100%);\n      }\n    }\n  }\n\n  // Non-responsive grid\n  @if $responsive == false {\n    .nonresponsive {\n      // Create columns\n      @for $i from 1 through $numColumns {\n        [#{$prefix}col~=\"#{$i}\"] {\n          width: (($i / $numColumns) * 100%);\n        }\n      }\n\n      // Offset for columns\n      @for $i from 1 through ($numColumns - 1) {\n        [#{$prefix}col~=\"+#{$i}\"] {\n          margin-left: (($i / $numColumns) * 100%);\n        }\n      }\n    }\n  }\n}\n","//\n// Base styles\n// =============================================================================\n\n//\n// Normalization\n// -------------\n// Courtesy of sanitize.css\n// (http://jonathantneal.github.io/sanitize.css/)\n\n:root {\n  -ms-overflow-style: -ms-autohiding-scrollbar;\n  overflow-y: scroll;\n  text-size-adjust: 100%;\n  -ms-text-size-adjust: 100%;\n  -webkit-text-size-adjust: 100%;\n}\n\naudio:not([controls]) {\n  display: none;\n}\n\ndetails {\n  display: block;\n}\n\ninput {\n  &[type=\"number\"] {\n    width: auto;\n  }\n\n  &[type=\"search\"] {\n    -webkit-appearance: textfield;\n\n    &::-webkit-search-cancel-button,\n    &::-webkit-search-decoration {\n      -webkit-appearance: none;\n    }\n  }\n}\n\nmain,\nsummary {\n  display: block;\n}\n\npre {\n  overflow: auto;\n}\n\nprogress {\n  display: inline-block;\n}\n\ntextarea {\n  overflow: auto;\n}\n\ntemplate,\n[hidden] {\n  display: none;\n}\n\n[unselectable] {\n  user-select: none\n}\n\n//\n// Universal Inheritance\n// ---------------------\n\n// Specify default styles in the document\n:root {\n  box-sizing: border-box;\n  color: inherit;\n  cursor: default;\n  vertical-align: top;\n}\n\n*,\n::after,\n::before {\n  box-sizing: border-box;\n  color: inherit;\n  font-family: inherit;\n  font-size: inherit;\n  line-height: inherit;\n  text-decoration: inherit;\n  vertical-align: inherit;\n}\n\n* {\n  //cursor: initial;\n  box-sizing: border-box;\n}\n\n//\n// Misc\n// ----\n\nhtml,\nbody {\n  height: 100%;\n  margin: 0;\n  padding: 0;\n}\n\na,\nbutton,\ninput,\nselect,\ntextarea {\n  //cursor: initial;\n}\n\n// RTL and LTR language support\n*[dir=\"rl;\"] {\n  direction: rtl;\n  unicode-bidi: embed;\n}\n\nbdo[dir=\"ltr\"],\nbdo[dir=\"rtl\"] {\n  unicode-bidi: bidi-override;\n}\n\nbdo[dir=\"ltr\"] {\n  direction: ltr;\n}\n\nbdo[dir=\"rtl\"] {\n  direction: rtl;\n}\n\nfigcaption {\n  color: inherit;\n  font-style: italic;\n  text-align: center;\n}\n\nspan {\n  vertical-align: top;\n}\n\n// fieldset reset\n\nbody:not(:-moz-handler-blocked) fieldset {\n  display: table-cell;\n}\n\n\nfieldset {\n  border: 0;\n  padding: 0;\n  margin: 0;\n  min-width: 0;\n}\n\nlegend {\n  display: block;\n  width: 100%;\n  padding: 0;\n  border: none;\n}","// Responsive utilities\n\n//\n// More easily include all the states for responsive-utilities.less.\n// [converter] $parent hack\n\n@mixin responsive-visibility($parent) {\n  #{$parent} {\n    display: block !important;\n  }\n  table#{$parent} {\n    display: table !important;\n  }\n  tr#{$parent} {\n    display: table-row !important;\n  }\n  th#{$parent},\n  td#{$parent} {\n    display: table-cell !important;\n  }\n}\n\n// [converter] $parent hack\n@mixin responsive-invisibility($parent) {\n  #{$parent} {\n    display: none !important;\n  }\n}","@include responsive-invisibility('.hide');\n@include responsive-visibility('.visible');\n\n//\n// Responsive: Utility classes\n// --------------------------------------------------\n\n// Visibility utilities\n// Note: Deprecated .visible-xs, .visible-sm, .visible-md, and .visible-lg as of v3.2.0\n\n@include responsive-invisibility('.visible-xs');\n@include responsive-invisibility('.visible-sm');\n@include responsive-invisibility('.visible-md');\n@include responsive-invisibility('.visible-lg');\n\n.visible-xs-block,\n.visible-xs-inline,\n.visible-xs-inline-block,\n.visible-sm-block,\n.visible-sm-inline,\n.visible-sm-inline-block,\n.visible-md-block,\n.visible-md-inline,\n.visible-md-inline-block,\n.visible-lg-block,\n.visible-lg-inline,\n.visible-lg-inline-block {\n  display: none !important;\n}\n\n@media (max-width: $bpXsMax) {\n  @include responsive-visibility('.visible-xs');\n}\n\n.visible-xs-block {\n  @media (max-width: $bpXsMax) {\n    display: block !important;\n  }\n}\n\n.visible-xs-inline {\n  @media (max-width: $bpXsMax) {\n    display: inline !important;\n  }\n}\n\n.visible-xs-inline-block {\n  @media (max-width: $bpXsMax) {\n    display: inline-block !important;\n  }\n}\n\n@media (min-width: $bpSm) and (max-width: $bpSmMax) {\n  @include responsive-visibility('.visible-sm');\n}\n\n.visible-sm-block {\n  @media (min-width: $bpSm) and (max-width: $bpSmMax) {\n    display: block !important;\n  }\n}\n\n.visible-sm-inline {\n  @media (min-width: $bpSm) and (max-width: $bpSmMax) {\n    display: inline !important;\n  }\n}\n\n.visible-sm-inline-block {\n  @media (min-width: $bpSm) and (max-width: $bpSmMax) {\n    display: inline-block !important;\n  }\n}\n\n@media (min-width: $bpMd) and (max-width: $bpMdMax) {\n  @include responsive-visibility('.visible-md');\n}\n\n.visible-md-block {\n  @media (min-width: $bpMd) and (max-width: $bpMdMax) {\n    display: block !important;\n  }\n}\n\n.visible-md-inline {\n  @media (min-width: $bpMd) and (max-width: $bpMdMax) {\n    display: inline !important;\n  }\n}\n\n.visible-md-inline-block {\n  @media (min-width: $bpMd) and (max-width: $bpMdMax) {\n    display: inline-block !important;\n  }\n}\n\n@media (min-width: $bpLg) {\n  @include responsive-visibility('.visible-lg');\n}\n\n.visible-lg-block {\n  @media (min-width: $bpLg) {\n    display: block !important;\n  }\n}\n\n.visible-lg-inline {\n  @media (min-width: $bpLg) {\n    display: inline !important;\n  }\n}\n\n.visible-lg-inline-block {\n  @media (min-width: $bpLg) {\n    display: inline-block !important;\n  }\n}\n\n@media (max-width: $bpXsMax) {\n  @include responsive-invisibility('.hidden-xs');\n}\n\n@media (min-width: $bpSm) and (max-width: $bpSmMax) {\n  @include responsive-invisibility('.hidden-sm');\n}\n\n@media (min-width: $bpMd) and (max-width: $bpMdMax) {\n  @include responsive-invisibility('.hidden-md');\n}\n\n@media (min-width: $bpLg) {\n  @include responsive-invisibility('.hidden-lg');\n}\n\n// Print utilities\n//\n// Media queries are placed on the inside to be mixin-friendly.\n\n// Note: Deprecated .visible-print as of v3.2.0\n\n@include responsive-invisibility('.visible-print');\n\n@media print {\n  @include responsive-visibility('.visible-print');\n}\n\n.visible-print-block {\n  display: none !important;\n\n  @media print {\n    display: block !important;\n  }\n}\n\n.visible-print-inline {\n  display: none !important;\n\n  @media print {\n    display: inline !important;\n  }\n}\n\n.visible-print-inline-block {\n  display: none !important;\n\n  @media print {\n    display: inline-block !important;\n  }\n}\n\n@media print {\n  @include responsive-invisibility('.hidden-print');\n}","//\n// Type Styles\n// =============================================================================\n\n//\n// Defaults\n// --------\n\ni,\nem {\n  font-style: italic;\n}\n\nb,\nstrong {\n  font-weight: bold;\n}\n\nsmall {\n  font-size: 75%;\n  vertical-align: baseline;\n}\n\nu {\n  text-decoration: underline;\n}\n\nsup,\nsub {\n  font-size: 75%;\n  line-height: 0;\n  position: relative;\n  vertical-align: baseline;\n}\n\nsup {\n  top: -0.5em;\n}\n\nsub {\n  bottom: -0.25em;\n}\n\ns {\n  text-decoration: line-through;\n}\n\n//\n// Paragraphs\n// --------\n\np {\n  margin-bottom: rem(16);\n}\n\n//\n// Links\n// -----\n\na {\n  cursor: pointer;\n  text-decoration: none;\n\n  &:hover,\n  &:focus,\n  &:active {\n    outline: none;\n  }\n}\n\n//\n// Abbreviations\n// -------------\n\nabbr[title] {\n  cursor: help;\n}\n\n//\n// Pre, Code, and User Input\n// -------------------------\n\ncode,\npre,\nkbd,\nsamp {\n  font-family: $monoFont;\n  font-size: em(14);\n}\n\ncode {\n  vertical-align: middle;\n  white-space: nowrap;\n}\n\npre code {\n  white-space: pre;\n}\n\nkbd {\n  display: inline-block;\n  line-height: 1.75;\n  white-space: nowrap;\n}\n\nsamp {\n  vertical-align: bottom;\n}\n\n//\n// Addresses\n// ---------\n\naddress {\n  font-style: normal;\n}\n\np {\n  font-size: em(16);\n  line-height: em(24, 16);\n\n  &.lead {\n    font-size: em(20);\n    line-height: em(28, 20);\n  }\n}","////\n/// @group size-functions\n/// @since 2.0.1\n/// @author Bernhard Ruoff\n/// @access public\n////\n\n/// Converts a px value into a rem value\n/// @example\n/// width: rem(32); // returns 2rem\n///\n/// @param {Number} $value - value in pixels\n/// @param {Number} $base [$base-size]- 16 !default\n/// @require {variable} $base-size\n///\n/// @return {Number} Value in rem\n\n@function rem($value, $base: $base-size) {\n  @return ($value / $base) + rem;\n}","////\n/// @group size-functions\n/// @since 2.0.1\n/// @author Bernhard Ruoff\n/// @access public\n////\n\n/// Converts a px value into a em value\n/// @example\n/// font-size: em(16); // returns 1em\n/// line-height: em(24, 12); // returns 2em\n///\n/// @param {Number} $value - value in pixels\n/// @param {Number} $base [$base-size]- 16 !default\n/// @require {variable} $base-size\n///\n/// @return {Number} Value in em\n\n@function em($value, $base: $base-size) {\n  @return ($value / $base) + em;\n}","// Type Helper classes\n\n.text-center {\n  text-align: center;\n}\n\n.text-right {\n  text-align: right;\n}\n\n.text-left {\n  text-align: left;\n}","//\n// Headings\n// =============================================================================\n\n\nh1, h2, h3, h4, h5, h6,\n.h1, .h2, .h3, .h4, .h5, .h6,\n.giga, .mega, .kilo {\n  font-weight: $headings-weight;\n\n  small,\n  .small {\n    vertical-align: baseline;\n  }\n}\n\nh1, h2, h3,\n.h1, .h2, .h3,\n.giga, .mega, .kilo {\n  margin: rem(16) 0 rem(8);\n\n  small,\n  .small {\n    font-size: 65%;\n  }\n}\n\nh4, h5, h6,\n.h4, .h5, .h6 {\n  margin: rem(16) 0 rem(8);\n\n  small,\n  .small {\n    font-size: 75%;\n  }\n}\n\n// Ratio for downscaling on mobile devices\n$ratioXS: .875;\n\nh1,\n.h1,\n.giga {\n  font-size: em($h1-size * $ratioXS);\n  line-height: em(($h1-height * $ratioXS), ($h1-size * $ratioXS));\n\n  @media (min-width: $bpSm) {\n    font-size: em($h1-size);\n    line-height: em($h1-height, $h1-size);\n  }\n}\n\nh2,\n.h2 {\n  font-size: em($h2-size * $ratioXS);\n  line-height: em(($h2-height * $ratioXS), ($h2-size * $ratioXS));\n\n  @media (min-width: $bpSm) {\n    font-size: em($h2-size);\n    line-height: em($h2-height, $h2-size);\n  }\n}\n\nh3,\n.h3,\n.mega {\n  font-size: em($h3-size * $ratioXS);\n  line-height: em(($h3-height * $ratioXS), ($h3-size * $ratioXS));\n\n  @media (min-width: $bpSm) {\n    font-size: em($h3-size);\n    line-height: em($h3-height, $h3-size);\n  }\n}\n\nh4,\n.h4 {\n  font-size: em($h4-size * $ratioXS);\n  line-height: em(($h4-height * $ratioXS), ($h4-size * $ratioXS));\n\n  @media (min-width: $bpSm) {\n    font-size: em($h4-size);\n    line-height: em($h4-height, $h4-size);\n  }\n}\n\nh5,\n.h5,\n.kilo {\n  font-size: em($h5-size * $ratioXS);\n  line-height: em(($h5-height * $ratioXS), ($h5-size * $ratioXS));\n\n  @media (min-width: $bpSm) {\n    font-size: em($h5-size);\n    line-height: em($h5-height, $h5-size);\n  }\n}\n\nh6,\n.h6 {\n  font-size: em($h6-size * $ratioXS);\n  line-height: em(($h6-height * $ratioXS), ($h6-size * $ratioXS));\n\n  @media (min-width: $bpSm) {\n    font-size: em($h6-size);\n    line-height: em($h6-height, $h6-size);\n  }\n}\n\n.base-font{\n  font-size: $base-size + px;\n  line-height: 1.5em;\n}","@mixin clearfix() {\n  &:before,\n  &:after {\n    content: \"\";\n    display: table;\n  }\n  &:after {\n    clear: both;\n  }\n}","////\n/// dl.scss\n/// @deprecated\n\n\ndl {\n  @include clearfix;\n\n  dt {\n    margin-bottom: rem(4);\n\n    @media (min-width: $bpSm) {\n      width: rem(80);\n      float: left;\n    }\n  }\n\n  dd {\n    margin-bottom: rem(4);\n\n    @media (min-width: $bpSm) {\n      margin-left: rem(96);\n    }\n  }\n}","fieldset {\n  border: none;\n  margin: 0;\n}","// Input Style\n\nlabel {\n  margin-bottom: rem(4);\n  display: block;\n}\n\ninput:not([type=\"submit\"]):not([type=\"reset\"]):not([type=\"checkbox\"]):not([type=\"radio\"]),\nselect,\ntextarea {\n  width: 100%;\n  display: block;\n  padding: rem(8);\n  min-height: rem(32);\n  outline: none;\n  margin-bottom: rem(4);\n\n  &:focus {\n    outline: none;\n  }\n\n  &[disabled] {\n    cursor: not-allowed;\n  }\n\n  &:-webkit-autofill,\n  &:-webkit-autofill:hover,\n  &:-webkit-autofill:focus,\n  &:-webkit-autofill:active {\n    transition: background-color 5000s ease-in-out 0s;\n    color: white;\n  }\n}\n\nselect {\n  height: rem(32);\n}\n\ninput:not([type=\"submit\"]):not([type=\"reset\"]) {\n  // Remove focus styles\n  &[type=\"checkbox\"],\n  &[type=\"radio\"],\n  &[type=\"range\"],\n  &[type=\"file\"],\n  &[type=\"color\"] {\n    &:focus {\n      border-color: transparent;\n      box-shadow: none;\n      outline: none;\n    }\n  }\n}\n\n// Keep the original size for this small inputs\ninput[type=\"checkbox\"],\ninput[type=\"file\"],\ninput[type=\"image\"],\ninput[type=\"radio\"] {\n  height: auto;\n  width: auto;\n  display: inline-block;\n}\n\n// Checkbox & radio\ninput[type=\"checkbox\"],\ninput[type=\"radio\"] {\n  line-height: normal;\n  padding: 0;\n  vertical-align: middle;\n}\n\n// Textarea\ntextarea {\n  height: auto;\n  resize: vertical;\n}\n\n// Style select like a standard input - Internet Explorer 11+\nselect {\n  padding: 0;\n\n  &[disabled] {\n  }\n\n  &[multiple] {\n    height: auto;\n  }\n\n  &::-ms-expand {\n    display: none;\n  }\n\n  &::-ms-value {\n    color: currentColor;\n  }\n\n  option {\n    padding: 0 .75em\n  }\n}\n\nfieldset{\n  padding: 0;\n\n  [data-row]{\n    margin: 0 rem(-8);\n\n    [data-col]{\n      padding-left: rem(8);\n      padding-right: rem(8);\n    }\n  }\n}","////\n/// @group reset\n/// @since 2.0.1\n/// @author Bernhard Ruoff\n/// @access public\n////\n\n/// appearance mixin\n/// @example\n/// button {\n///   @include appearance(none);\n/// }\n///\n/// @param {normal|icon|window|button|menu|field|none} $value\n\n@mixin appearance($value) {\n  -webkit-appearance: $value;\n  -moz-appearance: $value;\n  appearance: $value;\n}","@mixin user-select($value) {\n  -webkit-touch-callout: $value;\n  -webkit-user-select: $value;\n  -khtml-user-select: $value;\n  -moz-user-select: $value;\n  -ms-user-select: $value;\n  user-select: $value;\n}","// Button Basic Styles\n// ------------------------------------------------\n\ninput[type=submit],\ninput[type=reset],\nbutton,\n.btn {\n  // core styles\n  @include appearance(none);\n  @include user-select(none);\n  outline: none;\n  border: none;\n  display: inline-block;\n  text-align: center;\n  vertical-align: middle;\n  cursor: pointer;\n  touch-action: manipulation;\n  text-decoration: none;\n  position: relative;\n\n  // global styles\n  font-size: rem(16);\n  line-height: rem(24);\n  min-width: rem(40);\n  margin: rem(4);\n  padding: rem(8) rem(16);\n\n  &:active,\n  &:focus {\n    outline: none;\n  }\n\n  &.disabled,\n  &[disabled*='disabled'] {\n    opacity: .6;\n    cursor: not-allowed;\n    @include box-shadow(none);\n  }\n\n  // ec-icon styles for default btn\n\n  .ec-icon {\n    //cursor: inherit;\n    display: inline;\n\n    &:empty{\n      margin-left: rem(-16);\n      margin-right: rem(-16);\n\n      &:before{\n        padding-right: 0;\n      }\n    }\n\n    &:before {\n      padding-right: rem(8);\n    }\n  }\n\n  &.small {\n    font-size: rem(14);\n    line-height: rem(24);\n    min-width: rem(24);\n    padding: 0 rem(8);\n\n    .ec-icon {\n      &:before {\n        font-size: rem(16);\n        line-height: rem(24);\n        padding-right: rem(8);\n      }\n\n      &:empty{\n        margin-left: rem(-8);\n        margin-right: rem(-8);\n\n        &:before{\n          padding-right: 0;\n        }\n      }\n    }\n  }\n\n  &.big {\n    font-size: rem(20);\n    line-height: rem(28);\n    padding: rem(10) rem(16);\n\n    .ec-icon {\n      &:before {\n        font-size: rem(28);\n        line-height: rem(28);\n      }\n\n      &:empty{\n        margin-left: rem(-16);\n        margin-right: rem(-16);\n      }\n    }\n  }\n\n  &.block {\n    width: 100%;\n    display: block;\n    margin-left: 0;\n    margin-right: 0;\n  }\n}","////\n/// @group shadow\n/// @since 2.0.1\n/// @author Bernhard Ruoff\n/// @access public\n////\n\n/// cross-browser mixin for box-shadow\n/// @param {value} $value - can be multiple arguments\n///\n/// @example\n/// box-shadow(0 4px 16px rgba(black, .2), 0 4px 32px rgba(red, .1));\n\n@mixin box-shadow($value...) {\n  -webkit-box-shadow: $value;\n  -moz-box-shadow: $value;\n  box-shadow: $value;\n}","img{\n  &.responsive{\n    width: 100%;\n    height: auto;\n  }\n}","figure {\n  margin: 0;\n}",".pull-right {\n  float: right !important;\n}\n\n.pull-left {\n  float: left !important;\n}","$base-padding: rem(16);\n\n.padding {\n  padding: $base-padding;\n}\n\n.padding-top {\n  padding-top: $base-padding;\n}\n\n.padding-right {\n  padding-right: $base-padding;\n}\n\n.padding-bottom {\n  padding-bottom: $base-padding;\n}\n\n.padding-left {\n  padding-left: $base-padding;\n}\n\n.padding-horizontal {\n  padding-top: $base-padding;\n  padding-bottom: $base-padding;\n}\n\n.padding-vertical {\n  padding-left: $base-padding;\n  padding-right: $base-padding;\n}","ul.unstyled,\nol.unstyled {\n  margin: 0;\n  padding: 0;\n  list-style: none;\n}\n\nul.inline,\nol.inline {\n  margin: 0;\n  padding: 0;\n  list-style: none;\n\n  > li {\n    display: inline-block;\n  }\n}\n\n","%ellipsis {\n  -ms-text-overflow: ellipsis;\n  text-overflow: ellipsis;\n  overflow: hidden;\n  white-space: nowrap;\n}","////\n/// ec-icon placeholder based on ec-icon.1.1.9\n/// @author Bernhard Ruoff\n///\n/// can be used for :before and :after elements via @extand\n\n%ec-icon {\n  font-family: \"ec-icon\" !important;\n  -webkit-font-smoothing: antialiased;\n  -moz-osx-font-smoothing: grayscale;\n  font-weight: normal !important;\n  font-variant: normal !important;\n  font-style: normal;\n  text-transform: none !important;\n  text-rendering: optimizeLegibility;\n  speak: none;\n  display: inline-block;\n  letter-spacing: normal;\n  word-wrap: normal;\n  white-space: nowrap;\n  direction: ltr;\n  font-size: 1rem;\n  line-height: 1.5rem;\n}\n\n/// TODO delete after deployment of ec-icon 1.2.0\n\n.ec-icon {\n  &:before {\n    padding-right: rem(8);\n  }\n\n  &:empty {\n    &:before {\n      padding-right: 0;\n    }\n  }\n}","/// @author Bernhard Ruoff\n/// @since 0.3.5\n\nbody {\n  font-family: $bodyFont;\n  font-size: 14px;\n\n  @media (min-width: $bpSm) {\n    font-size: 100%;\n  }\n}\n\nh1, h2, h3, h4, h5, h6,\n.mega, .giga, .kilo {\n  font-family: $headerFont;\n}","//\n// @font-face\n// -------------\n@import url('https://fonts.googleapis.com/css?family=PT+Sans:400,700');\n@import url('https://icons.cdn.entrecode.de/ec-icons-1.0.9.css');\n\n$bodyFont: 'PT Sans', sans-serif;\n$headerFont: 'PT Sans', sans-serif;\n\n//\n// Color Palette\n// -------------\n\n$background: white;\n$text: #37474F;\n$link: #00B0FF;\n$super: #FF5353;\n$minor: #37474F;\n$highlight: #FFFF8D;\n\n// function for setColor\n@if (lightness($text) > 50) {\n  $setDark: $background;\n  $setLight: $text;\n} @else {\n  $setDark: $text;\n  $setLight: $background;\n}\n\n// radius of input-elements\n$input-radius: 4 !default;\n\n// radius of buttons\n// if $btn-round === false\n$btn-radius: 4 !default;\n\n// if is btn is round\n$btn-round: false;\n\n// min width for wide buttons\n$btn-wide-width: 240px;\n\n// radius of dropdown-elements\n$dropdown-radius: 4 !default;\n\n// headings variables\n$base-size: 16;\n\n$headings-weight: bold;\n\n$h1-size: 48;\n$h1-height: 56;\n\n$h2-size: 40;\n$h2-height: 48;\n\n$h3-size: 32;\n$h3-height: 40;\n\n$h4-size: 24;\n$h4-height: 32;\n\n$h5-size: 20;\n$h5-height: 28;\n\n$h6-size: 16;\n$h6-height: 24;\n\n// Breakpoint for responsive navigation\n$bpNav: $bpLg;","/// embossed styles for extension\n\n%embossed {\n  &%btn {\n    @include box-shadow(0 4px 8px rgba(black, 0.1), 0 4px 24px rgba(black, 0.1));\n\n    &:hover {\n      @include box-shadow(0 2px 4px rgba(black, 0.1), 0 2px 8px rgba(black, 0.1));\n    }\n    //position: relative;\n    //\n    //&:after {\n    //  content: '';\n    //  position: absolute;\n    //  bottom: 0;\n    //  left: 10%;\n    //  width: 80%;\n    //  height: 80%;\n    //  background-color: darken(getColor(base, link), 10%);\n    //  @include filter(blur(6px));\n    //  @include border-radius(inherit);\n    //  @include transition(all .2s);\n    //  z-index: -1;\n    //  opacity: 1;\n    //}\n    //\n    //&:hover {\n    //  &:after {\n    //    @include filter(blur(6px));\n    //    bottom: 6%;\n    //  }\n    //}\n    //\n    //&.super {\n    //  &:after {\n    //    background-color: getColor(base, super);\n    //  }\n    //}\n  }\n}\n\n/// shadow styles for extension\n\n%shadow {\n  &%hover {\n    @include box-shadow(0 0 0 2px getColor(state, hover));\n  }\n\n  &%input {\n    @include box-shadow(0 4px 16px rgba(getColor(base, super), .2), 0 4px 32px rgba(getColor(base, super), .1));\n  }\n\n  &%dropdown {\n    @include box-shadow(0 4px 8px rgba(black, 0.1), 0 4px 24px rgba(black, 0.1));\n  }\n\n  &%overlay {\n    @include box-shadow(0 4px 8px rgba(black, 0.1), 0 4px 24px rgba(black, 0.1));\n\n    &%big {\n      @include box-shadow(0 8px 16px rgba(black, 0.1), 0 8px 48px rgba(black, 0.2));\n    }\n  }\n\n  &%selected {\n    @include box-shadow(0 0 0 4px getColor(btn, minor));\n  }\n}","////\n/// colors.scss - 1.0.0\n/// @author Bernhard Ruoff\n///\n/// color map for xlcss based projects\n/// @param {variable} $colors [$colors] - set a custom variable for each project\n/// @require custom variable for $colors\n///\n/// TODO check for simplification\n/// TODO color function for btn.minor, btn.minorHover, state.hover\n\n// colormap\n$t5: (\n        base: (\n                \"text\": $text,\n                \"background\": $background,\n                \"link\": $link,\n                \"linkHover\": varColor($link, -10),\n                \"super\": $super,\n                \"superHover\": varColor($super, -10),\n                \"highlight\": $highlight,\n                \"divider\": rgba($text, .1),\n                \"border\": borderColor($background, $text)\n        ),\n        btn: (\n                \"default\": $link,\n                \"defaultHover\": varColor($link, -10),\n                \"super\": $super,\n                \"superHover\": varColor($super, -10),\n                \"minor\": rgba($minor, .1),\n                \"minorHover\": rgba($minor, .2),\n                \"pill\": rgba($text, .1),\n                \"pillHover\": rgba($text, .2)\n        ),\n        text: (\n                \"invert\": $background,\n                \"highlight\": $highlight,\n                \"light\": rgba($text, .6),\n                \"lighter\": rgba($text, .4),\n                \"lightest\": rgba($text, .2),\n        ),\n        background: (\n                \"invert\": $text,\n                \"light\": setColor($background, darken($background, 12), lighten($background, 12)),\n                \"lighter\": setColor($background, darken($background, 8), lighten($background, 8)),\n                \"lightest\": setColor($background, darken($background, 4), lighten($background, 4)),\n                \"backdrop\": setColor($background, rgba($text, .8), rgba($background, .8)),\n        ),\n        state: (\n                \"hover\": rgba(desaturate($link, 60), .1),\n                \"selected\": rgba(desaturate($link, 20), .4),\n                \"active\": $link,\n                \"disabled\": rgba($text, .2),\n                \"notification\": systemColor(#80D8FF),\n                \"error\": systemColor(red),\n                \"success\": systemColor(#00E676),\n                \"warning\": systemColor(orange),\n                \"urgend\": $super,\n                \"marked\": $highlight\n        )\n) !default;\n\n// set custom map-name for getColor function\n$colors: $t5;\n","/// @author Bernhard Ruoff\n/// @since 0.3.6\n///\n/// TODO check for redundance with ec/vc\n\nbody {\n  color: getColor(base, text);\n}\n\na {\n  color: getColor(base, link);\n  @include transition(color .2s);\n  cursor: pointer;\n\n  * {\n    cursor: pointer;\n  }\n\n  &:hover {\n    color: getColor(base, linkHover);\n  }\n}\n\np {\n  margin: 0 0 rem(8) 0;\n\n  &.small {\n    vertical-align: baseline;\n    font-size: rem(12);\n    line-height: rem(16);\n  }\n\n  &.big,\n  &.lead {\n    font-size: em(18);\n    line-height: em(28, 18);\n    font-weight: lighter;\n  }\n\n  strong,\n  small {\n    vertical-align: baseline;\n  }\n}\n\np,\nsmall,\nstrong,\nem,\ni {\n  //@include state(text);\n}\n\n.text-default {\n  color: getColor(base, text) !important;\n}\n\n.text-light {\n  color: getColor(text, light) !important;\n}\n\n.text-lighter {\n  color: getColor(text, lighter) !important;\n}\n\n.text-lightest {\n  color: getColor(text, lightest) !important;\n}\n\n.text-invert {\n  color: getColor(text, invert) !important;\n}\n\n.text-link {\n  color: getColor(base, link) !important;\n}\n\n.text-super {\n  color: getColor(base, super) !important;\n}\n\n.text-highlight {\n  color: getColor(base, highlight) !important;\n}\n\n.text-error {\n  color: getColor(state, error) !important;\n}\n\n.text-success {\n  color: getColor(state, success) !important;\n}\n\nmark {\n  background-color: transparent;\n  color: getColor(base, highlight);\n}\n\n.nowrap {\n  white-space: nowrap;\n  vertical-align: baseline;\n}\n\nhr {\n  margin: rem(32) 0;\n  border: 0;\n  border-bottom: 1px dashed getColor(base, divider);\n}\n\n.ellipsis {\n  @extend %ellipsis;\n  display: block;\n}","@mixin transition($transition...) {\n  -moz-transition: $transition;\n  -o-transition: $transition;\n  -webkit-transition: $transition;\n  transition: $transition;\n  //-webkit-backface-visibility: hidden;\n}\n\n@mixin transition-property($property...) {\n  -moz-transition-property:    $property;\n  -o-transition-property:      $property;\n  -webkit-transition-property: $property;\n  transition-property:         $property;\n}\n\n@mixin transition-duration($duration...) {\n  -moz-transition-property:    $duration;\n  -o-transition-property:      $duration;\n  -webkit-transition-property: $duration;\n  transition-property:         $duration;\n}\n\n@mixin transition-delay($delay...) {\n  -moz-transition-delay:    $delay;\n  -o-transition-delay:      $delay;\n  -webkit-transition-delay: $delay;\n  transition-delay:         $delay;\n}","////\n/// @group color-functions\n/// @since 2.0.1\n/// @author Bernhard Ruoff\n/// @access public\n////\n\n/// generate darker or lighter variation\n/// @link https://medium.com/@erikdkennedy/color-in-ui-design-a-practical-framework-e18cacd97f9e#.78i84mre8\n/// @example\n///  color: varColor(#ff0000, 20); // returns color: #fe6e67;\n///\n/// @param {Color} $source - source color\n/// @param {number} $amount - positive or negative number for color transformation\n///\n/// @return {Color} color variation\n\n@function varColor($source, $amount) {\n  /// @prop {value} $hue: hue value of source color\n  $hue: hue($source);\n  /// @prop {value} $lightness: lightness value of source color\n  $lightness: lightness($source);\n  /// @prop {value} $sat: saturation value of source color\n  $sat: saturation($source);\n  /// @prop {number} $hueRatio: ratio for increasing or decreasing hue based on $amount\n  $hueRatio: .15;\n  /// @prop {number} $satRatio: ratio for increasing or decreasing saturation based on $amount\n  $satRatio: .66;\n  /// @prop {value} $target: target color value\n  $target: null;\n\n  @if ($amount > 0) {\n    /// if $amount is positive\n    /// color transformation to lighter and less saturated.\n    /// also the hue is transformed depending on its value\n\n    /// @prop {value} $satTransform: value for desaturation\n    $satTransform: $amount * $satRatio;\n    /// @prop {value} $hueTransform: value for hue adjustment\n    $hueTransform: $amount * $hueRatio;\n\n    @if ($lightness == 100 and $sat == 0 or $lightness == 100 and $sat == 0) {\n\n    } @else {\n      /// if next to red hue is transformed towards red (0)\n      @if ($hue < 60 and $hue >= 0) {\n        $source: adjust-hue($source, $hueTransform);\n      }\n\n      /// if next to green hue is transformed towards green (120)\n      @if ($hue < 120 and $hue >= 60) {\n        $source: adjust-hue($source, $hueTransform *-1);\n      }\n\n      /// if next to green hue is transformed towards green (120)\n      @if ($hue < 180 and $hue >= 120) {\n        $source: adjust-hue($source, $hueTransform);\n      }\n\n      /// if next to blue hue is transformed towards blue (240)\n      @if ($hue < 240 and $hue >= 180) {\n        $source: adjust-hue($source, $hueTransform *-1);\n      }\n\n      /// if next to blue hue is transformed towards blue (240)\n      @if ($hue < 300 and $hue >= 240) {\n        $source: adjust-hue($source, $hueTransform);\n      }\n\n      /// if next to red hue is transformed towards red (360)\n      @if ($hue <= 360 and $hue >= 300) {\n        $source: adjust-hue($source, $hueTransform *-1);\n      }\n    }\n\n    $target: lighten(desaturate($source, $satTransform), $amount);\n  } @else {\n\n    /// if $amount is negative\n    /// color transformation to darker and more saturated.\n    /// also the hue is transformed depending on its value\n\n    /// make value of $amount positive for transformation\n    $amount: $amount * -1;\n    /// @prop {value} $satTransform: value for desaturation\n    $satTransform: $amount * $satRatio;\n    /// @prop {value} $hueTransform: value for hue adjustment\n    $hueTransform: $amount * $hueRatio;\n\n    @if ($lightness == 100 and $sat == 0 or $lightness == 100 and $sat == 0) {\n\n    } @else {\n      /// if next to red hue is transformed towards red (0)\n      @if ($hue < 60 and $hue >= 0) {\n        $source: adjust-hue($source, $hueTransform *-1);\n      }\n\n      /// if next to green hue is transformed towards green (120)\n      @if ($hue < 120 and $hue >= 60) {\n        $source: adjust-hue($source, $hueTransform);\n      }\n\n      /// if next to green hue is transformed towards green (120)\n      @if ($hue < 180 and $hue >= 120) {\n        $source: adjust-hue($source, $hueTransform *-1);\n      }\n\n      /// if next to blue hue is transformed towards blue (120)\n      @if ($hue < 240 and $hue >= 180) {\n        $source: adjust-hue($source, $hueTransform);\n      }\n\n      /// if next to blue hue is transformed towards blue (120)\n      @if ($hue < 300 and $hue >= 240) {\n        $source: adjust-hue($source, $hueTransform *-1);\n      }\n\n      /// if next to red hue is transformed towards red (360)\n      @if ($hue <= 360 and $hue >= 300) {\n        $source: adjust-hue($source, $hueTransform);\n      }\n    }\n\n    $target: darken(saturate($source, $satTransform), $amount);\n  }\n\n  @return $target;\n}","////\n/// headings - 1.0.0\n/// @author Bernhard Ruoff\n\nh1, h2, h3, h4, h5, h6,\n.h1, .h2, .h3, .h4, .h5, .h6,\n.giga, .mega, .kilo {\n  font-weight: $headings-weight;\n\n  small,\n  .small {\n    vertical-align: baseline;\n  }\n}\n\nh1, h2, h3, h4, h5, h6,\n.h1, .h2, .h3, .h4, .h5, .h6,\n.giga, .mega, .kilo {\n  margin: rem(16) 0 rem(8);\n\n  small,\n  .small {\n    font-size: 87.5%;\n  }\n\n  .big {\n    font-size: 112.5%;\n  }\n}\n\n// Ratio for downscaling on mobile devices\n$ratioXS: .875;\n\nh1,\n.h1,\n.giga {\n  font-size: em($h1-size * $ratioXS);\n  line-height: em(($h1-height * $ratioXS), ($h1-size * $ratioXS));\n\n  @media (min-width: $bpSm) {\n    font-size: em($h1-size);\n    line-height: em($h1-height, $h1-size);\n  }\n}\n\nh2,\n.h2 {\n  font-size: em($h2-size * $ratioXS);\n  line-height: em(($h2-height * $ratioXS), ($h2-size * $ratioXS));\n\n  @media (min-width: $bpSm) {\n    font-size: em($h2-size);\n    line-height: em($h2-height, $h2-size);\n  }\n}\n\nh3,\n.h3,\n.mega {\n  font-size: em($h3-size * $ratioXS);\n  line-height: em(($h3-height * $ratioXS), ($h3-size * $ratioXS));\n\n  @media (min-width: $bpSm) {\n    font-size: em($h3-size);\n    line-height: em($h3-height, $h3-size);\n  }\n}\n\nh4,\n.h4 {\n  font-size: em($h4-size * $ratioXS);\n  line-height: em(($h4-height * $ratioXS), ($h4-size * $ratioXS));\n\n  @media (min-width: $bpSm) {\n    font-size: em($h4-size);\n    line-height: em($h4-height, $h4-size);\n  }\n}\n\nh5,\n.h5,\n.kilo {\n  font-size: em($h5-size * $ratioXS);\n  line-height: em(($h5-height * $ratioXS), ($h5-size * $ratioXS));\n\n  @media (min-width: $bpSm) {\n    font-size: em($h5-size);\n    line-height: em($h5-height, $h5-size);\n  }\n}\n\nh6,\n.h6 {\n  font-size: em($h6-size * $ratioXS);\n  line-height: em(($h6-height * $ratioXS), ($h6-size * $ratioXS));\n\n  @media (min-width: $bpSm) {\n    font-size: em($h6-size);\n    line-height: em($h6-height, $h6-size);\n  }\n}\n\n.base-font {\n  font-size: $base-size + px;\n  line-height: 1.5em;\n}","////\n/// TODO check for more modular, adaptable solution\n\ntable {\n  border-collapse: collapse;\n  border-spacing: 0;\n  empty-cells: show;\n  width: 100%;\n\n  thead {\n  }\n\n  tbody {\n    tr {\n      td {\n        padding: rem(8);\n      }\n      th {\n        text-align: left;\n        padding: rem(8);\n        padding-top: rem(16);\n      }\n    }\n  }\n}\n\n[data-container] {\n  &.table {\n    display: table;\n    width: 100%;\n    padding: 0;\n\n    [data-row] {\n      display: table-row;\n\n      [data-col] {\n        display: table-cell;\n        vertical-align: middle;\n      }\n    }\n  }\n}\n\n/// table styles for divs\n.table {\n  display: table;\n  border-collapse: collapse;\n  width: 100%;\n\n  &.fixed {\n    table-layout: fixed;\n  }\n}\n\n.table-row {\n  display: table-row;\n}\n\n.table-cell {\n  display: table-cell;\n\n  &.center {\n    height: 100%;\n    vertical-align: middle;\n    text-align: center;\n  }\n}\n","////\n/// global button styles - 1.0.1\n/// @author Bernhard Ruoff\n///\n/// @param {boolean} $btn-round - can be set in config.scss\n\ninput[type=submit],\ninput[type=reset],\nbutton,\n.btn {\n  background-color: getColor(btn, default);\n  color: setColor(getColor(btn, default));\n  font-weight: normal;\n  @include border-radius(rem($btn-radius));\n  position: relative;\n  @include transition(background-color .4s);\n  cursor: pointer;\n  height: auto;\n\n  * {\n    cursor: pointer;\n  }\n\n  strong {\n    vertical-align: baseline;\n  }\n\n  .pill {\n    background-color: getColor(base, background);\n    font-size: rem(12);\n    font-weight: bold;\n    line-height: rem(16);\n    margin: 0;\n    margin-right:rem(4);\n    color: getColor(base, link);\n\n    &:hover {\n      background-color: getColor(base, background);\n      color: getColor(base, link);\n    }\n  }\n\n  .bubble {\n    position: absolute;\n    top: 0;\n    right: rem(-8);\n  }\n\n  .label {\n    pointer-events: none;\n  }\n\n  // default :hover and .active state\n  &.active,\n  &:hover,\n  &:focus {\n    background-color: getColor(btn, defaultHover);\n    color: setColor(getColor(btn, default));\n  }\n\n  // little transform animation onclick\n  &:active {\n    @include transform(translate3d(0, 2px, 0));\n  }\n\n  &.dark {\n    background-color: varColor(getColor(btn, default),-20);\n    color: setColor(varColor(getColor(btn, default),-20));\n  }\n\n  // minor button style, for secondary or minor interactions\n\n  &.minor {\n    background-color: getColor(btn, minor);\n    color: setColor(getColor(btn, minor));\n\n    &.active,\n    &:hover,\n    &:focus {\n      background-color: getColor(btn, minorHover);\n      color: setColor(getColor(btn, minor));\n    }\n\n    &.invert {\n      color: getColor(text, invert);\n\n      &.active,\n      &:hover,\n      &:focus {\n        color: getColor(text, invert);\n      }\n    }\n  }\n\n  // super button style, for call to action\n\n  &.super {\n    background-color: getColor(btn, super);\n    color: setColor(getColor(btn, super));\n\n    &.active,\n    &:hover,\n    &:focus {\n      background-color: getColor(btn, superHover);\n      color: setColor(getColor(btn, super));\n    }\n  }\n\n  // clear button style, for link-alike buttons\n  &.clear {\n    background-color: transparent;\n    color: getColor(base, link);\n\n    &.active,\n    &:hover,\n    &:focus {\n      background-color: getColor(state, hover);\n      color: getColor(base, linkHover);\n    }\n\n    &.invert {\n      color: getColor(text, invert);\n\n      &.active,\n      &:hover,\n      &:focus {\n        color: getColor(text, invert);\n      }\n    }\n  }\n\n  // outlined button style\n\n  &.outlined {\n    background-color: transparent;\n    color: getColor(base, link);\n    @include box-shadow(0 0 0 1px getColor(base, link));\n\n    &.active,\n    &:hover,\n    &:focus {\n      color: getColor(base, linkHover);\n      background-color: transparent;\n      @include box-shadow(0 0 0 2px getColor(base, linkHover));\n    }\n\n    &.invert {\n      @include box-shadow(0 0 0 1px getColor(text, invert));\n      color: getColor(text, invert);\n      font-weight: bold;\n\n      &.active,\n      &:hover,\n      &:focus {\n        background-color: transparent;\n        @include box-shadow(0 0 0 2px getColor(text, invert));\n        color: getColor(text, invert);\n      }\n    }\n  }\n\n  // additional embossed class\n  // TODO check for implementation in config.scss\n\n  &.embossed {\n    @extend %embossed%btn;\n  }\n\n  // style for ec-icon within a button\n  .ec-icon {\n    display: inline;\n\n    &:empty {\n      margin: 0 rem(-8);\n    }\n\n    &:before {\n      vertical-align: top;\n    }\n  }\n\n  // small buttons\n  &.small {\n    height: auto;\n    @include border-radius(rem($btn-radius * .5));\n  }\n\n  // big buttons\n  // TODO make style for big buttons\n\n  &.big {\n    height: auto;\n  }\n\n  // buttons with a minimum width\n  &.wide {\n    min-width: $btn-wide-width;\n  }\n\n  &.round {\n    @include border-radius(rem(20));\n\n    &.small {\n      @include border-radius(rem(16));\n    }\n\n    &.big {\n      @include border-radius(rem(24));\n    }\n  }\n\n  // full-width buttons\n  &.block {\n    width: 100%;\n    display: block;\n    margin-left: 0;\n    margin-right: 0;\n  }\n}\n\n/// sets buttons to rounded\n/// @param {boolean} $btn-round - can be set in config.scss\n\n@if $btn-round == true {\n  input[type=submit],\n  input[type=reset],\n  button,\n  .btn {\n    @include border-radius(rem(20));\n\n    &.small {\n      @include border-radius(rem(16));\n    }\n\n    &.big {\n      @include border-radius(rem(24));\n    }\n  }\n}","////\n/// @group border-radius\n/// @since 2.0.1\n/// @author Bernhard Ruoff\n/// @access public\n////\n\n/// cross-browser mixin for border-radius\n/// @param {value} $radius - can be multiple arguments\n\n@mixin border-radius($radius) {\n  -webkit-border-radius: $radius;\n  border-radius: $radius;\n  // stops bg color from leaking outside the border:\n  background-clip: padding-box;\n}","////\n/// @group color-functions\n/// @since 2.1.5\n/// @author Bernhard Ruoff\n/// @access public\n////\n\n/// sets color based on luminosity of another color\n/// @example\n/// color: setColor(#222222); // returns #fff\n///\n/// @param {Color} $color - Name of base color\n/// @param {Color} $setDark [#000] - Name of dark color\n/// @param {Color} $setLight [#fff] - Name of light color\n///\n/// @return {Color} value of $setLight or $setDark\n\n@function setColor($color, $setDark: #000, $setLight: #fff) {\n  $hue: hue($color);\n\n  @if (opacity($color) > .5) {\n\n    /// if color is not a light color (yellow, cyan)\n    @if ($hue >= 190 or $hue <= 40) {\n      @if (lightness($color) >= 80) {\n        @return $setDark; // Lighter background, return dark color\n      } @else {\n        @return $setLight; // Lighter background, return dark color\n      }\n    } @else {\n      @if (lightness($color) >= 40) {\n        @return $setDark; // Lighter background, return dark color\n      } @else {\n        @return $setLight; // Lighter background, return dark color\n      }\n    }\n  } @else {\n    @return $setDark;\n  }\n}","// generic transform\n@mixin transform($transforms) {\n  -moz-transform: $transforms;\n  -o-transform: $transforms;\n  -ms-transform: $transforms;\n  -webkit-transform: $transforms;\n  transform: $transforms;\n  -webkit-backface-visibility: hidden;\n}\n\n@mixin transform-style($val) {\n  -webkit-transform-style: $val;\n  -moz-transform-style: $val;\n  -ms-transform-style: $val;\n  transform-style: $val;\n}\n\n//transform origin\n@mixin transform-origin($origin) {\n  moz-transform-origin: $origin;\n  -o-transform-origin: $origin;\n  -ms-transform-origin: $origin;\n  -webkit-transform-origin: $origin;\n  transform-origin: $origin;\n}","////\n/// caret.scss - 1.0.0\n/// can be extended in :before or :after element or uses directly\n///\n/// @author Bernhard Ruoff\n\n.caret {\n  border: rem(4) solid transparent;\n  border-top-color: getColor(text, lighter);\n  content: '';\n  width: rem(8);\n  height: rem(8);\n  margin-top: rem(-2);\n  position: absolute;\n  top: 50%;\n  right: rem(16);\n}","/// form.scss - 1.1.0\n/// @author Bernhard Ruoff\n\n%input {\n  border: 1px solid getColor(base, border);\n  background-color: getColor(base, background);\n  color: getColor(base, text);\n  font-size: rem(16);\n  line-height: rem(24);\n  padding: rem(4) rem(16);\n  @include border-radius(rem($input-radius));\n  margin: rem(4) 0;\n  text-align: left;\n  min-height: initial;\n\n  @include placeholder {\n    font-size: rem(16);\n    line-height: rem(24);\n    color: getColor(text, light);\n  }\n\n  &:focus,\n  &:hover {\n    border-color: darken(getColor(base, border), 20);\n    @extend %shadow%hover;\n    @include placeholder {\n      color: getColor(base, text);\n    }\n  }\n\n  &:focus:hover {\n    @include placeholder {\n      color: getColor(text, lightest);\n    }\n  }\n\n  &.disabled,\n  &[disabled] {\n    cursor: not-allowed;\n    background-color: getColor(background, lightest);\n    border-color: getColor(background, light);\n  }\n\n  &.small {\n    font-size: rem(14);\n    padding-top: 0;\n    padding-bottom: 0;\n  }\n\n  &.embossed {\n    border: none;\n    @extend %shadow%dropdown;\n  }\n\n  &.big {\n    padding-top: rem(8);\n    padding-bottom: rem(8);\n  }\n}\n\ninput:not([type=\"submit\"]):not([type=\"reset\"]):not([type=\"checkbox\"]):not([type=\"radio\"]),\nselect,\ntextarea {\n  @extend %input;\n}\n\n/// reset number input\n\ninput[type=number]::-webkit-inner-spin-button {\n  -webkit-appearance: none;\n}\n\nform {\n\n  // Overrides the gutter of the xlcss grid\n\n  fieldset {\n    [data-row] {\n      margin-left: rem(-8);\n      margin-right: rem(-8);\n    }\n\n    [data-col] {\n      padding-left: rem(8);\n      padding-right: rem(8);\n    }\n  }\n  pre {\n    background-color: getColor(background, lightest);\n    color: getColor(text, light);\n    padding: rem(16);\n    margin: rem(8);\n    max-height: 200px;\n    overflow-y: scroll;\n  }\n}\n\nlabel {\n  margin: 0 0 rem(4) 0;\n  color: getColor(base, text);\n  cursor: pointer;\n  display: block;\n  font-size: rem(14);\n  @include transition(color .2s);\n  @include clearfix;\n\n  a, small, strong {\n    display: inline-block;\n  }\n\n  small {\n    display: inline-block;\n    font-size: rem(12);\n    line-height: rem(20);\n    color: getColor(text, light);\n\n    &:last-child {\n      margin: rem(4) 0;\n    }\n  }\n\n  p, small {\n    &:empty {\n      display: none;\n    }\n  }\n\n  &:hover {\n    color: getColor(base, text);\n  }\n\n  &.disabled {\n    cursor: not-allowed;\n\n    &:hover {\n      color: getColor(base, text);\n    }\n\n    &:not(.active) {\n      opacity: .4;\n\n      &:hover {\n        opacity: .2;\n      }\n    }\n  }\n\n  input[type=\"checkbox\"],\n  input[type=\"radio\"] {\n\n    &:hover {\n      @extend %shadow%hover;\n    }\n\n    & + small {\n      display: inline-block;\n      cursor: pointer;\n    }\n  }\n\n  &.text-center {\n    input:not([type=\"submit\"]):not([type=\"reset\"]):not([type=\"checkbox\"]):not([type=\"radio\"]),\n    select,\n    textarea {\n      text-align: center;\n    }\n  }\n\n  &.mandatory {\n    input:not([type=\"submit\"]):not([type=\"reset\"]):not([type=\"checkbox\"]):not([type=\"radio\"]),\n    select,\n    textarea {\n      @include box-shadow(0 0 0 2px rgba(getColor(base, link), .6));\n    }\n  }\n}\n\nfieldset {\n  margin-bottom: rem(8);\n}","@mixin placeholder {\n  &::-webkit-input-placeholder {\n    @content\n  }\n  &:-moz-placeholder {\n    @content\n  }\n  &::-moz-placeholder {\n    @content\n  }\n  &:-ms-input-placeholder {\n    @content\n  }\n}","form {\n  &.ng-submitted {\n    input:not([type=\"submit\"]):not([type=\"reset\"]):not([type=\"checkbox\"]):not([type=\"radio\"]),\n    select,\n    textarea {\n\n      &.ng-invalid-required.ng-invalid.ng-empty {\n        border-color: getColor(state, error);\n      }\n\n      &.ng-invalid.ng-touched {\n        border-color: getColor(state, error);\n      }\n    }\n  }\n\n  input[type=\"email\"] {\n    &:not(.ng-empty) {\n      &.ng-dirty.ng-valid {\n        @include box-shadow(inset 0 0 2px 2px rgba(getColor(state, success), .4) !important);\n      }\n    }\n  }\n}","////\n/// @group color-functions\n/// @since 2.0.1\n/// @author Bernhard Ruoff\n/// @access public\n////\n\n/// generate color based on a set of colors\n/// @example\n/// color: systemColor(#00E676); // returns a color with a hue value of 151\n///\n/// @access public\n/// @param {Color} $target - name of target color\n/// @param {String} $colors [$link $super $minor $highlight] - string of colors\n///\n/// @return {Color} color with harmonic lightness and saturation\n\n@function systemColor($target, $colors: $link $super $minor $highlight) {\n  $targetHue: hue($target);\n  $totalHue: 0;\n  $totalSat: 0;\n  $totalLight: 0;\n\n  @each $current-color in $colors {\n    $i: index($colors, $current-color);\n    $totalHue: $totalHue + hue($current-color);\n    $totalSat: $totalSat + saturation($current-color);\n    $totalLight: $totalLight + lightness($current-color);\n  }\n\n  $totalHue: $totalHue / length($colors);\n  $totalSat: $totalSat / length($colors);\n  $totalLight: $totalLight / length($colors);\n\n  @return hsl($targetHue, $totalSat, $totalLight);\n}","/// alert.scss - 1.0.0\n///\n/// @author Bernhard Ruoff\n/// @example\n///   <p class=\"alert error\">Error Message</p>\n\n.alert {\n  padding: rem(4) rem(8);\n  margin: rem(8) 0;\n  @include border-radius(2px);\n  font-size: em(14);\n  line-height: rem(20);\n  background-color: getColor(background, lightest);\n  text-align: center;\n\n  &.notification {\n    background-color: getColor(state, notification);\n    color: setColor(getColor(state, notification));\n  }\n\n  &.error {\n    background-color: getColor(state, error);\n    color: setColor(getColor(state, error));\n  }\n\n  &.success {\n    background-color: getColor(state, success);\n    color: setColor(getColor(state, success));\n  }\n\n  &.warning {\n    background-color: getColor(state, warning);\n    color: setColor(getColor(state, warning));\n  }\n\n  &.outlined {\n    border: 1px solid getColor(base, divider);\n    background-color: transparent;\n  }\n}","////\n/// TODO check style\n\n$button-width: 40px; // The width of the button area\n$button-height: 40px; // The height of the button area\n$bar-thickness: 2px; // The thickness of the button bars\n$button-pad: 12px; // The left/right padding between button area and bars.\n$button-bar-space: 4px; // The spacing between button bars\n$button-transistion-duration: 0.3s; // The transition duration\n\n.hamburger {\n  position: absolute;\n  top: 0;\n  right: 0;\n  z-index: 100;\n  width: $button-width;\n  height: $button-height;\n  border-radius: ($button-width * .5);\n  background-color: transparent;\n\n  @media (min-width: $bpNav) {\n    display: none;\n  }\n\n  span {\n    display: block;\n    position: absolute;\n    top: ($button-height / 2) - ($bar-thickness / 2);\n    left: $button-pad;\n    right: $button-pad;\n    height: $bar-thickness;\n    background-color: white;\n    @include transition(background-color 0s $button-transistion-duration);\n\n    &:before,\n    &:after {\n      content: '';\n      position: absolute;\n      display: block;\n      left: 0;\n      width: 100%;\n      height: $bar-thickness;\n      background-color: white;\n    }\n\n    &:before {\n      top: -$bar-thickness - $button-bar-space;\n      @include transition(top $button-transistion-duration $button-transistion-duration, transform $button-transistion-duration 0s);\n    }\n\n    &:after {\n      bottom: -$bar-thickness - $button-bar-space;\n      @include transition(bottom $button-transistion-duration $button-transistion-duration, transform $button-transistion-duration 0s);\n    }\n  }\n\n  &.active {\n    position: fixed;\n\n    span {\n      background-color: transparent;\n\n      &:before {\n        top: 0;\n        @include transform(rotate(45deg));\n        @include transition(top $button-transistion-duration 0s, transform $button-transistion-duration $button-transistion-duration);\n      }\n\n      &:after {\n        bottom: 0;\n        @include transform(rotate(-45deg));\n        @include transition(bottom $button-transistion-duration 0s, transform $button-transistion-duration $button-transistion-duration);\n      }\n    }\n  }\n}\n\n","////\n/// label - 1.0.0\n/// @author Bernhard Ruoff\n\n.label {\n  font-size: rem(12);\n  line-height: rem(16);\n  font-weight: bold;\n  white-space: nowrap;\n  display: inline-block;\n  padding: 0 rem(8);\n\n  &.light,\n  &.inactive {\n    background-color: getColor(btn, minor);\n    color: getColor(text, light);\n  }\n\n  &.urgend {\n    background-color: getColor(state, urgend);\n    color: setColor(getColor(state, urgend), getColor(base, text), white);\n  }\n\n  &.notification {\n    background-color: getColor(state, notification);\n    color: setColor(getColor(state, notification), getColor(text, light), white);\n  }\n\n  &.active {\n    background-color: getColor(base, link);\n    color: setColor(getColor(base, link), getColor(text, light), white);\n  }\n\n  &.super {\n    background-color: getColor(base, super);\n    color: setColor(getColor(base, super), getColor(text, light), white);\n  }\n}","////\n/// pill.scss - 1.0.0\n/// @author Bernhard Ruoff\n\n.pill {\n  display: inline-block;\n  vertical-align: middle;\n  font-size: rem(14);\n  line-height: rem(24);\n  padding: 0 rem(8);\n  margin: rem(4);\n  @include border-radius(rem(2));\n  background-color: getColor(btn, pill);\n  @include transition(all .2s);\n  overflow: hidden;\n  white-space: nowrap;\n  @include user-select(none);\n  position: relative;\n\n  &:hover {\n    background-color: getColor(btn, pillHover);\n  }\n\n  .ec-icon {\n    display: inline;\n\n    &:before {\n      vertical-align: top;\n    }\n  }\n\n  .bubble {\n    font-size: 1em;\n    line-height: rem(16);\n    background-color: white;\n    margin: rem(4) rem(-8);\n    margin-right: rem(8);\n  }\n\n  &.super {\n    background-color: rgba(getColor(base, background), .6);\n    border: 1px solid getColor(base, link);\n\n    &:hover {\n      background-color: getColor(base, background);\n    }\n  }\n\n  a {\n    color: getColor(text, light);\n    margin-right: rem(-8);\n\n    &:hover {\n      .ec-icon.close {\n        color: getColor(state, error);\n      }\n    }\n  }\n\n  &.small {\n    font-size: rem(12);\n    line-height: rem(20);\n  }\n\n  &.reset {\n    position: relative;\n    padding-right: rem(32);\n\n    &:after {\n      content: '\\00d7';\n      position: absolute;\n      color: getColor(text, lighter);\n      right: 0;\n      top: 0;\n      padding: 0 rem(8);\n    }\n  }\n}\n\na.pill {\n  cursor: pointer;\n  user-select: none;\n\n  * {\n    cursor: pointer;\n    user-select: none;\n  }\n}","////\n/// bubble.scss - 1.0.0\n/// @author Bernhard Ruoff\n///\n/// TODO additional styles for other states\n\n.bubble {\n  font-size: em(10);\n  line-height: rem(16);\n  font-weight: bold;\n  white-space: nowrap;\n  display: inline-block;\n  padding: 0 rem(8);\n  @include border-radius(rem(8));\n\n  &.urgend {\n    background-color: getColor(state, urgend);\n    color: setColor(getColor(state, urgend));\n  }\n}\n\n","////\n/// header - 1.0.0\n\n.header {\n  display: table;\n  vertical-align: middle;\n  width: 100%;\n  //margin-botto