    margin-top: 1px;\n\n    li {\n      display: block;\n      text-align: left;\n\n      &.select-options-intro {\n        background-color: getColor(background, lightest);\n        color: getColor(text, light);\n        text-align: center;\n        padding: rem(4) rem(8);\n        font-size: rem(12);\n        vertical-align: baseline;\n      }\n\n      .btn {\n        @extend .btn.block;\n        @include border-radius(rem($btn-radius));\n        text-align: left;\n        position: relative;\n\n        .label {\n          position: absolute;\n          right: rem(8);\n          top: 50%;\n          @include transform(translateY(-50%));\n          @extend .label.light;\n        }\n      }\n\n      &.divider {\n        margin: rem(8) 0;\n        background-color: getColor(base, divider);\n        height: 1px;\n      }\n    }\n  }\n\n  &.search {\n    > li {\n      padding-left: rem(40) !important;\n\n      &:after {\n        display: none;\n      }\n\n      &:before {\n        @extend %ec-icon;\n        content: \"\\e0e5\";\n        position: absolute;\n        color: getColor(text, light);\n        left: rem(16);\n        top: 50%;\n        @include transform(translateY(-50%));\n        z-index: 1;\n      }\n\n      ul.select-options {\n        li {\n\n          &.select-options-intro {\n            text-align: left;\n            padding-left: rem(36);\n          }\n        }\n      }\n    }\n  }\n\n  &.big {\n    > li {\n      @extend %input.big;\n    }\n  }\n}","// dropdown.scss\n// 1.0.0\n// @author Bernhard Ruoff, Michael Locher\n// ------\n//\n// css only dropdown\n//\n// @example\n//  <ul class=\"dropdown\">\n//    <li tabindex=\"1\">\n//      Label\n//      <ul class=\"dropdown-select\">\n//        <li>\n//          <span class=\"btn clear\">Label</span>\n//        </li>\n//        <li class=\"divider\"></li>\n//        <li>\n//          <span class=\"btn clear\">Label</span>\n//        </li>\n//      </ul>\n//    </li>\n//  </ul>\n\nul.dropdown {\n  @exten